<template>
  <configure-cron-form
    :valid="valid"
    :error="error"
    :onSave="onSave"
    :minutes="minutes"
    :onCancel="onCancel"
    :onDelete="onDelete"
    :isInitialized="isInitialized"
    :displayName="service.display_name"
    v-on:updateValid="valid=$event"
    v-on:updateMinutes="minutes = $event"
  ></configure-cron-form>
</template>
<script>
import _get from 'lodash/get';
import FormError from '../Common/FormError.vue';
import ConfigureCronForm from '../Common/ConfigureCronForm.vue';
import * as appsService from '../../services/appsService';
import * as rubixService from '../../services/rubixService';
import ServiceCronPicker from '../Common/ServiceCronPicker.vue';
import * as slaveAppsService from '../../services/slaveAppsService';

export default {
  name: 'configure-cron-view',
  props: ['service', 'onCancel', 'sync', 'slaveId', 'isSystemService'],
  components: { ServiceCronPicker, FormError, ConfigureCronForm },
  data() {
    return {
      valid: false,
      minutes: 0,
      error: {
        message: null,
      },
      isInitialized: false,
    };
  },
  mounted() {
    this.minutes = _get(this.service, 'restart_job.timer', 0);
    this.error.message = _get(this.service, 'restart_job.error', null);
    this.isInitialized = true;
  },
  methods: {
    getApiService() {
      if (this.service.slaveId) {
        return { service: slaveAppsService, slaveId: this.service.slaveId };
      }

      if (this.isSystemService) {
        return { service: rubixService, slaveId: null };
      }

      return { service: appsService, slaveId: null };
    },
    onSave() {
      const payload = {
        timer: +this.minutes || 0,
        service: this.service.service,
      };
      const { service, slaveId } = this.getApiService();
      service.createRestartJob(payload, slaveId).then(() => {
        this.$toasted.show('Successfully updated');
        this.onCancel();
        this.syncDetails();
      });
    },
    syncDetails() {
      if (typeof this.sync === 'function') {
        this.sync();
      }
    },
    onDelete() {
      const payload = {
        service: this.service.service,
      };
      const { service, slaveId } = this.getApiService();

      service.deleteRestartJob(payload, slaveId).then(() => {
        this.$toasted.show('Successfully deleted');
        this.onCancel();
        this.syncDetails();
      });
    },
  },
};
</script>
